* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.site-title {
  font-size: 72px;
  background: linear-gradient(to right, #c6ffdd, #fbd786, #f7797d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  margin-bottom: 30px;
}

.auth-GH-btn {
  display: flex;
  align-items: center;
  height: 30px;
  padding: 20px;
  background-color: pink;
  border-radius: 12px;
  color: white;
  cursor: pointer;

  border: none;
}

.auth-GH-btn:hover {
  background-color: rgba(255, 192, 203, 0.6);
}

a {
  text-decoration: none;
}

.repo-list-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.repo-list-heading {
  margin: 30px 0;
  height: 100px;
  color: pink;
  font-size: 64px;
}

.repo-list-content-container {
  padding: 40px;
  border-radius: 8px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  margin-bottom: 20px;
}

.repo-list-item {
  background-color: rgb(216, 233, 245);
  min-height: 100px;
  width: 500px;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 16px;

  background-image: linear-gradient(to left, #c6ffdd, #fbd786, #f7797d);
}

.repo-list-item:hover {
  transform: scale(1.03);
}

.repo-list-item h2 {
  color: azure;
  font-size: 24px;
}
.repo-list-item p {
  font-size: 12px;
  font-weight: 600;
  color: beige;
}

.repo-owner-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: azure;
  min-height: 40px;
}

.repo-owner-wrapper h4 {
  font-size: 16px;
}

.repo-owner-wrapper img {
  height: 35px;
  border-radius: 50%;
}
